.animated-logo-container {
  perspective: 500px;
  height: 500px;
  display: flex;
  justify-items: center;
  align-items: center;
  width: 500px;
  margin: 0 auto;
  perspective-origin: center;
}
.animated-logo-container .fas {
  font-size: 20rem;
  position: absolute;
  left: 50%;
  top: 50%;
  color: var(--theme-color);
  transform: translateX(-50%) translateY(-50%);
}
.animated-logo-container div {
  width: 300px;
  height: 300px;
  border: 2rem solid var(--theme-color);
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  border-radius: 50%;
}
.animated-logo-container div.ring-one {
  /* border-color: red; */
  transform: translateX(-50%) translateY(-50%) rotateX(0deg) rotateX(85deg);
}
.animated-logo-container div.ring-two {
  transform: translateX(-50%) translateY(-50%) rotateY(105deg) rotateX(130deg);
  /* border-color: green; */
}
.animated-logo-container div.ring-three {
  transform: translateX(-50%) translateY(-50%) rotateY(86deg) rotateX(50deg);
  /* border-color: orange; */
}
