.card {
  position: relative;
  height: 100%;
}
.card-circle-one {
  position: absolute;
  width: 200px;
  height: 200px;
  transform: translateX(65%) translateY(50%);
  border-radius: 50%;
  background: var(--btn-gradient);
  right: 0;
  top: 35%;
  transition: all 1s ease;
}
.card:hover .card-circle-one {
  transform: translateX(65%) translateY(55%);
}
.card-circle-two {
}
.card:hover .card-circle-two {
  transform: translateX(86%) translateY(43%);
}
.card > span {
  display: block;
  z-index: 1;
  overflow: hidden;
  position: absolute;
  z-index: 1;
  top: 1rem;
  right: 0;
  overflow: hidden;
  width: 10rem;
  height: 10rem;
  border-radius: var(--radius);
}
.card > span > .card-blob {
  display: block;
  border-radius: 50%;
  z-index: 1;
  background-color: var(--theme-color);
  height: 9rem;
  position: relative;
  left: 2rem;
  top: -3rem;
}
.card .background {
  z-index: auto;
  right: 20px;
  width: 100%;
  border-radius: var(--radius);
  height: 300px;
  top: 60%;
  margin-bottom: 3rem;
  background-position: center;
  background-size: 200%;
  background-repeat: no-repeat;
}
.card:hover .background {
  /* filter: blur(0px);
  background-size: 230%; */
}
.card .background::after {
  content: "";
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  position: absolute;
}
.card .card-icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  vertical-align: top;
  font-size: 2.4rem;
  font-weight: bolder;
  height: 6rem;
  width: 6rem;
  margin-right: 3rem;
  text-align: center;
  line-height: 1;
  background: var(--border-color);
  border-radius: 0%;
  padding: 2rem;
  margin-left: 0rem;
  margin-bottom: 2rem;
  margin-top: 0rem;
  /* box-shadow: -8rem -8rem 10rem 10rem var(--main-font-color); */
  color: var(--theme-color);
  transition: all 0.4s ease;
  /* opacity: 0.4; */
}
.card-link:hover .card-icon {
  box-shadow: -2rem -2rem 10rem 0rem var(--main-font-color);
  background-color: var(--main-background-color);
}
.card {
}
.card-busy > div {
  filter: grayscale(1);
  opacity: 0.2;
}
.card-children {
  display: inline-block;
  z-index: 3;
  width: 100%;
  text-align: left;
  position: relative;
}
.card.colored > div:not(.svg-icon) {
  background-image: linear-gradient(357deg, #00000022, transparent);
  background-color: var(--theme-color);
}
.card.alt {
  background-image: none;
  box-shadow: none;
}
.card.alt > div {
  background-color: var(--card-background);
  box-shadow: none;
}
.card > div {
}
.card .card > div {
  padding: 2rem 3rem;
  background: var(--main-background-color);
}
.card .card {
  margin: 0rem 0;
  padding-top: 2rem !important;
  padding-bottom: 0;
  text-align: left;
}
.card > span.status {
}
/* .card-actions {
  padding-top: 8rem;
}
.card-actions > div {
  position: absolute;
  bottom: 0rem;
  left: 0rem;
} */
.card button {
  /* width: auto; */
}

.card .right-component {
  position: absolute;
  right: 6rem;
  top: 0;
  transform-origin: right;
  transform: scale(0.4) translateY(-50%);
}
.card .svg-icon {
  width: 100%;
  right: 0;
  height: auto;
  padding: 0;
  top: 0;
  z-index: 11;
  background: transparent;
}
.card .svg-icon img {
  height: 23rem;
}
.card-user-dp {
  width: 100px;
  height: 100px;
  border-radius: 50%;
}

.card-link .header {
  margin-bottom: 0.3rem;
  line-height: 1.3;
}

/* Portrait */
@media only screen and (min-device-width: 1024px) and (max-device-width: 1366px) and (orientation: portrait) and (-webkit-min-device-pixel-ratio: 1.5) {
  /* .card-actions {
