.expandable-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.expandable-header button {
  border: none;
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  cursor: pointer;
}
.expandable-header.onExpand button {
  width: 10rem !important;
  border-radius: var(--radius);
  font-weight: bold;
}
.expandable-header .header {
  margin-bottom: 0 !important;
}
@media only screen and (max-width: 768px) {
  .card .svg-icon {
    position: absolute;
    opacity: 0.1;
    left: 50%;
    top: 3rem;
    width: 50%;
    height: 113%;
    z-index: -1;
  }
  .card .svg-icon > img {
    width: 100% !important;
    height: 100% !important;
  }
  .card-children {
    position: relative;
    font-size: 2rem;
  }
  .card-children .btn-primary {
    /* width: 100%; */
    margin-right: 0rem !important;
  }
  .card-children .btn-primary {
    margin-right: 1rem !important;
  }

  .card-actions {
    padding-top: 0em;
  }
  .card-actions > div {
    position: relative;
    bottom: 0;
    left: 0;
  }
  .card > div {
    padding: 3rem 2rem;
  }
  .card .card-icon {
    width: 8rem;
    height: 8rem;
    font-size: 3rem;
    margin-bottom: 1rem;
    margin-left: 0;
  }
  .card .right-component {
    position: absolute;
    right: 3rem;
    top: 0;
    transform-origin: right;
    transform: scale(0.13) translateY(-312%);
  }
}

@media only screen and (max-width: 500px) {
  .card .background {
    height: 200px;
  }
  .normal-pad {
    padding: 0rem 2rem;
  }
}
@media only screen and (max-width: 300px) {
  .card .background {
    height: 140px;
  }
  .card > div {
    padding: 4rem;
  }
}

.alert-container {
  position: fixed;
  left: 0%;
  width: 100%;
  overflow: hidden;
  z-index: 1900;
  border-radius: var(--radius);
  top: 11rem;
  line-height: 1.5;
  transform: translateY(-400%);
  transition: all 1s ease;
  box-shadow: 0 2rem 5rem 1rem var(--shadow-color);
}
.alert-container .fas:not(.fa-times),
.alert-container .fa:not(.fa-times) {
  margin-right: 2rem;
  font-size: 4rem;
}
.alert-container > div {
  font-size: 2rem;
  padding: 1rem 3rem;
  color: var(--white);
  padding-left: 4rem;
  padding-right: 4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 1rem 4rem;
}
.alert-container > div > div {
  display: flex;
  align-items: center;
}
.open {
  transform: translateY(0%);
}
.alert-close {
  font-size: 2rem;
  color: var(--white);
  opacity: 1;
  background: transparent;
  border: none;
  width: 5rem;
  height: 5rem;
  cursor: pointer;
  border-radius: 50%;
}
.alert-close:hover {
  background: #ffffff29;
}
.alert-container .success-alert {
  background-color: var(--success-font-color);
}
.alert-container .info-alert {
  background-color: var(--info-font-color);
}
.alert-container .error-alert {
  background-color: var(--error-font-color);
}
@media only screen and (max-width: 560px) {
  .alert-container > div {
    padding: 2rem 3rem 2rem 2rem;
    font-size: 2.5rem;
  }
  .alert-container {
    top: 0;
  }
}

.pwa-installer-footer .card-v2 {
  padding: 3rem 4rem;
  background-color: var(--theme-color);
  border-radius: var(--radius);
}
@keyframes move-up {
  from {
    opacity: 0;
    transform: translateY(200%);
  }
  to {
    opacity: 1;
    pointer-events: all;
    transform: translateY(0%);
  }
}

.pwa-drawer-hidden,
.pwa-footer-hidden {
  opacity: 0;
  display: none;
}
.pwa-installer-footer img {
  border-radius: 50%;
}
.pwa-installer-footer .card-v2 h1 {
  color: var(--main-background-color);
  margin: 0;
}
.pwa-installer-footer .card-v2 button {
  margin-top: 3rem;
  padding: 1.5rem 3rem;
  border-radius: var(--radius);
  box-shadow: none;
  border: none;
  color: var(--theme-color);
  background-color: var(--main-background-color);
  font-weight: bold;
}

.pwa-mobile {
  position: fixed;
  left: 0;
  bottom: 0;
  border-radius: var(--radius);
  z-index: 200;
  opacity: 0;
  transition: var(--transition);
  transform: translateY(200%);
  animation: move-up 0.5s ease 1 forwards 6s;
}
.pwa-mobile .card-v2 {
  border-radius: var(--radius);
}
.pwa-mobile p {
  color: var(--main-background-color);
}
.pwa-mobile .card-v2 button {
  padding: 1rem 2rem;
}
.pwa-installer-footer .card-v2 .text-btn {
  margin-right: 1rem;
  background-color: transparent;
  color: var(--main-background-color);
}
.pwa-mobile-hidden {
  animation: move-up 0.2s ease 1 forwards reverse;
}

.policies-header {
  display: flex;
  justify-content: center;
  align-items: center;
}
.policies-content {
  padding: 10px;
}

.policies-container p {
  margin: 3rem 0;
}

