  --button-color: #fff;
  --green: rgb(0, 144, 24);
  --purple: rgb(100, 0, 194);
  --blue: rgb(0, 105, 224);
  --red: rgb(182, 9, 9);
  --orange: rgb(144, 70, 0);
  --light-green: rgba(0, 175, 29, 0.123);
  --light-purple: rgba(100, 0, 194, 0.164);
  --light-blue: rgba(0, 104, 224, 0.219);
  --light-red: rgba(182, 9, 9, 0.246);
  --light-orange: rgba(144, 70, 0, 0.226);
  --sub-title-color: #888;
  --description-color: #999;
  --secondary-link-background: #eee;
  --iphone-frame-back: linear-gradient(145deg, #f0f0f0, #cacaca);
  --iphone-frame-shadows: 20px 20px 50px #bebebe, -20px -20px 50px #ffffff;
  --iphone-notch-color: #eaeaea;
  --btn-primary-shadow: 0 5px 20px 10px #fe97003b;
}

body {
  color: var(--main-font-color);
  font-size: 1.6rem;
  font-family: var(--font-one);
  overflow-x: hidden;
  overflow-y: auto;
  padding-bottom: 10rem;
}
.blur-content {
  opacity: 0.1;
  transition: all 0.4s ease-in-out;
  pointer-events: none;
}
.flex-between {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.chip {
  margin-left: 0.8rem;
  background: var(--main-background-color);
  border-radius: var(--radius);
  color: var(--header-color);
  padding: 1rem 2rem;
  line-height: 0;
  font-size: 1.5rem;
  line-height: 1.2;
  display: inline-flex;
  justify-content: center;
  vertical-align: top;
  text-transform: capitalize;
  align-items: center;
}
textarea {
  font-size: inherit;
}
.MuiPaper-root {
  background-color: var(--card-background) !important;
  border-radius: var(--radius) !important;
}
.MuiPaper-root ul {
}
.MuiPaper-root ul li {
  font-size: 16px;
}
.main-content {
  position: relative;
  transition: all 0.2s ease;
  z-index: 4;
  padding: 8rem 0;
}
.ms-theme .ms-theme-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: var(--main-background-color);
  transition: all 0.2s ease-in-out;
  background-size: cover;
  background-position: center;
  filter: grayscale(1);
}
.ms-theme .ms-theme-gradient {
  position: relative;
}
.ms-theme .ms-theme-gradient::after {
  content: "";
  opacity: 0.99;
  position: fixed;
  left: 0;
  top: 0;
  width: 200vw;
  height: 200vh;
  z-index: -1;
  /* animation: move-background 10s linear infinite; */
  background: var(--main-background-color); /* fallback for old browsers */
}

.fileContainer .uploadPictureContainer {
  padding: 0 !important;
}
.fileContainer .uploadPictureContainer > .uploadPicturesWrapper > div {
  justify-content: flex-start;
}
.fileContainer .uploadPictureContainer {
  border: none;
  box-shadow: none;
  border-radius: 0 !important;
}
.fileContainer .uploadPictureContainer img.uploadPicture {
  border-radius: var(--radius);
  height: auto !important;
  width: 100% !important;
}
.fileUploader .fileContainer .deleteImage {
  position: absolute;
  top: -9px;
  right: -9px;
  color: var(--white);
  background: var(--theme-color);
  border-radius: 50%;
  text-align: center;
  cursor: pointer;
  font-size: 13px;
  font-weight: bold;
  line-height: 30px;
  width: 30px;
  height: 30px;
}

.overflow-hidden {
  overflow: hidden;
  height: 100vh;
}
.overflow-blur {
  opacity: 0.05;
  filter: grayscale(1);
}
@keyframes move-background {
  0% {
    transform: translateX(0%) translateY(0%);
  }
  25% {
    transform: translateX(-100vw) translateY(0%);
  }
  50% {
    transform: translateX(-100vw) translateY(-100vh);
  }
  75% {
    transform: translateX(0%) translateY(-100vh);
  }
  100% {
    transform: translateX(0%) translateY(0%);
  }
}
.hide {
  animation: hide 0.3s forwards;
}
.invisible {
  display: none;
}
@keyframes hide {
  0% {
    opacity: 1;
  }
  90% {
    opacity: 0;
  }
  100% {
    opacity: 0;
    display: none;
  }
}
