.dialog {
  width: 100%;
  height: 100vh;
  position: fixed;
  bottom: 0;
  z-index: 101;
  left: 0;
  display: none;
  justify-content: center;
  transition: all 0.5s ease;
  align-items: flex-end;
  /* opacity: 0; */
}
.dialog .dialog-underlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: var(--dialog-background);
  animation: dialog-animation 0.5s ease forwards;
}
@keyframes dialog-animation {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.dialog.open {
  display: flex;
}

.dialog-content > button.dialog-close {
  font-size: 2.5rem;
  border: transparent;
  color: var(--main-font-color);
  background-color: var(--main-background-color);
  position: absolute;
  /* bottom: 0; */
  right: 3rem;
  cursor: pointer;
  /* opacity: 0.3; */
  top: 3rem;
  box-shadow: none;
  background: transparent;
  /* -webkit-transform: translateX(-70%) translateY(-70%); */
  transform: translateX(0) translateY(0);
  width: 6rem;
  height: 6rem;
  border: 1px solid var(--border-color);
  border-radius: 50%;
  transition: var(--small-transition);
}
.dialog-content > button:hover {
  border: 1px solid var(--main-font-color);
}

.dialog.open > .dialog-content {
  width: 100%;
  max-width: 1024px;
  height: auto;
  min-height: 200px;
  max-height: 300px;
  overflow-y: auto;
  padding: 4rem;
  padding-top: 0;
  background-color: var(--main-background-color);
  position: relative;
  z-index: 4;
  margin-top: -3rem;
  border-radius: var(--radius) var(--radius) 0 0;
  box-shadow: var(--dialog-shadow);
  animation: slide-from-bottom 0.5s ease forwards;
  transform-origin: center;
  transform-style: preserve-3d;
}
.dialog-content .dialog-header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  padding-top: 3rem;
  padding-bottom: 1rem;
  background-color: var(--main-background-color);
}
.dialog.right {
  justify-content: flex-end;
  top: 0;
  bottom: unset;
}
.dialog.right > .dialog-content {
  width: 50vw;
  height: 102vh;
  max-height: 100vh;
  overflow-y: auto;
  border-radius: var(--radius);
  animation: slide-from-right 0.5s ease forwards;

  padding: 4rem;
}

@keyframes slide-from-bottom {
  0% {
    transform: translateY(60%);
  }

  100% {
    transform: translateY(0%);
  }
}
@keyframes slide-from-right {
  0% {
    transform: translateX(100%);
  }

  100% {
    transform: translateX(0%);
  }
}
.dialog.big.open > .dialog-content {
  width: 100%;
  min-height: 100vh;
  max-height: 100vh;
  overflow-y: auto;
  padding: 5% 9rem 0rem;
  background-color: var(--main-background-color);
  position: relative;
  z-index: 4;
  margin-top: 0rem;
  box-shadow: 0;
  border-radius: var(--radius);
  animation: slide-from-bottom 0.2s ease-out forwards;
}
.dialog.big.open .dialog-content > button {
  transform: none;
  right: 6rem;
  top: 6rem;
  font-size: 4rem;
}
.right .dialog-content > button {
  position: absolute;
  right: 2rem;
  top: 3rem;
  font-size: 3rem;
  background: var(--card-background);
  width: 6rem;
  height: 6rem;
  color: var(--main-font-color);
  border-radius: 50%;
  opacity: 1;
}
@media only screen and (max-width: 768px) {
  .dialog {
    width: 95% !important;
    left: 2.5% !important;
  }
  /* .dialog.big.open > .dialog-content {
    min-height: 94vh;
    max-height: 94vh;
  } */
  .dialog.big.open > .dialog-content {
    padding: 10% 3rem 0rem;
  }
  .dialog.open > .dialog-content {
